import React from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap';

function Footer() {
  return (
    <footer
      className="bg-dark text-white mt-"
      style={{
        width: '100%',
        margin: 0,
        padding: '10px 0',                                              // Reduced vertical padding (top & bottom)
                                                                           // Optionally set minHeight if you want a fixed height
        minHeight: '70px',
      }}
    >
      <Container>
        <Row>
          <Col md={4} className="mb-2">
           <h5 style={{ marginBottom: '8px',color:'#FF0000', textDecorationLine:'underline', textDecorationColor:'yellow' }}>Fitness App</h5>
            <p style={{ marginBottom: 0, lineHeight: 1.3 }}>
             A fitness app is a mobile application designed to promote physical activity, 
             healthy habits, and overall well-being
            </p>
          </Col>
          <Col md={4} className="mb-2">
            <h5 style={{ marginBottom: '8px',color:'#FF0000' }}>Quick Links</h5>
            <Nav className="flex-column">
              <Nav.Link href="#home" className="text-white py-0"> Home</Nav.Link>
              <Nav.Link href="#services" className="text-white py-0">Services</Nav.Link>
              <Nav.Link href="#contact" className="text-white py-0">  Contact</Nav.Link>
            </Nav>
          </Col>
          <Col md={4} className="mb-2">
            <h5 style={{ marginBottom: '8px',color:'#FF0000' }}>Contact Info</h5>
            <ul className="list-unstyled" style={{ marginBottom: 0, lineHeight: 1.3 }}>
             <li> Email: fit@example.com</li>
             <li> Phone: +91 99441-56231</li>
            <li>   Address: 2X6H+J73, Papanaickenpalayam, Coimbatore</li>
            </ul>
          </Col>
        </Row>
        <Row className="mt-3">
          <Col className="text-center" style={{ fontSize: '14px' }}>
            <p style={{ marginBottom: 0 }}>
              &copy; {new Date().getFullYear()} Your Company. All rights reserved.
            </p>
          </Col>
        </Row>
      </Container>
    </footer>
  );
}

export default Footer;
