import React, { useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import gymImage from '../assets/gym.jpg';
import Footer from '../components/Footer';                  

function Home() {
  const navigate = useNavigate();
  const [showAboutMore, setShowAboutMore] = useState(false);
  const [showServicesMore, setShowServicesMore] = useState(false);

  return (
    <div style={{ overflowX: 'hidden' }}>                    {/* Full-Width Hero Section bg ymg bag imag e  */}
      
      <div
        style={{
          backgroundImage: `url(${gymImage})`,
          backgroundSize: 'cover',
          backgroundPosition: 'center',
          backgroundRepeat: 'no-repeat',
          height: '100vh',
          width: '100vw',
          color: 'white',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          textAlign: 'center',
          padding: 0,
          margin: 0,
          boxSizing: 'border-box'
        }}
      >
        <h1 className="heading-3d" style={{ fontFamily: 'Times New Roman', fontWeight: 'bold',  }}>
          Welcome to Fitness App
        </h1>
        <p style={{ fontSize: '1.2rem', maxWidth: '600px', textAlign: 'center' }}>
          There are 5 components of physical fitness that are essential for overall health.
        </p>
        <Button
          variant="primary"
          onClick={() => navigate('/dashboard')}
          style={{ padding: '12px 30px' }}
        >
          Go to Dashboard
        </Button>
      </div>


      <Footer />
    </div>
  );
}

export default Home;
