import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "react-bootstrap"; 

const WorkoutList = () => {
  const [workouts, setWorkouts] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    const mockWorkouts = [
      { id: 1, name: "Push Ups", duration: 15, calories: 100 },
      { id: 2, name: "Running", duration: 30, calories: 250 },
      { id: 3, name: "Cycling", duration: 45, calories: 400 },
    ];
    setWorkouts(mockWorkouts);
  }, []);

  return (
    <div
      style={{
        minHeight: "100vh",
        backgroundImage: `url("https://images.pexels.com/photos/414029/pexels-photo-414029.jpeg")`,
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundRepeat: "no-repeat",
        padding: "40px",
        color: "#fff",
        fontFamily: "Arial, sans-serif",
      }}
    >
       <div className="d-flex justify-content-end mb-3">
        <Button
          variant="danger"
          onClick={() => navigate("/home")}
          style={{ fontWeight: "bold" }}
        >
           Back to Home
        </Button>
      </div>
      
      <div
        style={{
          background: "rgba(0, 0, 0, 0.6)",
          padding: "30px",
          borderRadius: "10px",
          maxWidth: "600px",
          margin: "0 auto",
        }}
      >
        <h2 style={{ textAlign: "center", marginBottom: "20px" }}>Workout List</h2>
        {workouts.length === 0 ? (
          <p>No workouts available</p>
        ) : (
          <ul style={{ listStyleType: "none", padding: 0 }}>
            {workouts.map((workout) => (
              <li key={workout.id} style={{ marginBottom: "20px" }}>
                <h4 style={{ marginBottom: "5px" }}>{workout.name}</h4>
                <p> {workout.duration} minutes</p>
                <p> {workout.calories} calories</p>
              </li>
            ))}
          </ul>
        )}
      </div>
    </div>
  );
};

export default WorkoutList;
