import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import {
  Navbar as BSNavbar,
  Nav,
  Container,
  Button,
  Image
} from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import FitnessLogo from './utils/Fitness.png';

const Navbar = () => (
  <BSNavbar bg="dark" variant="dark" expand="lg" sticky="top">
    <Container>
      {/* Brand with logo */}
      <LinkContainer to="/home">
        <BSNavbar.Brand className="d-flex align-items-center">
          <Image
            src={FitnessLogo}
            alt="Fitness Logo"
            width="50"
            height="50"
            className="me-2"
            rounded
          />
          <span style={{ color: 'red', fontWeight: 'bold', fontSize: '30px' }}>
            FITNESS
          </span>
        </BSNavbar.Brand>
      </LinkContainer>

      {/* Hamburger menu toggle for mobile */}
      <BSNavbar.Toggle aria-controls="basic-navbar-nav" />

      {/* Navigation links */}
      <BSNavbar.Collapse id="basic-navbar-nav">
        <Nav className="ms-auto align-items-center gap-2">
          <LinkContainer to="/home">
            <Nav.Link className="nav-link-home">Home</Nav.Link>
          </LinkContainer>

          <LinkContainer to="/login">
            <Nav.Link className="nav-link-home"></Nav.Link>
          </LinkContainer>

          <LinkContainer to="/workoutform">
            <Nav.Link className="nav-link-home">Workout Form</Nav.Link>
          </LinkContainer>

          <LinkContainer to="/workoutlist">
            <Nav.Link className="nav-link-home">Workout List</Nav.Link>
          </LinkContainer>

          <LinkContainer to="/register">
            <Nav.Link className="nav-link-home">Dashboard</Nav.Link>
          </LinkContainer>

          {/* Sign Up button as a styled link */}
          <LinkContainer to="/register">
            <Button variant="danger" className="ms-2">Sign Up</Button>
          </LinkContainer>
        </Nav>
      </BSNavbar.Collapse>
    </Container>
  </BSNavbar>
);

export default Navbar;
