import React, { useState } from 'react';
import { Form, Button, Container, Row, Col, Card, Alert } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';

function Login() {
  const [formData, setFormData] = useState({ email: '', password: '' });
  const [errorMsg, setErrorMsg] = useState('');
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData(prev => ({ ...prev, [e.target.name]: e.target.value }));
    setErrorMsg('');
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const users = JSON.parse(localStorage.getItem('users') || '[]');
    const foundUser = users.find(u => u.email === formData.email);

    if (!foundUser) {
      setErrorMsg('No account found with this email. Please register first.');
      return;
    }

    if (foundUser.password !== formData.password) {
      setErrorMsg('Invalid password. Please try again.');
      return;
    }

    localStorage.setItem('currentUser', JSON.stringify(foundUser));
    navigate('/dashboard');
  };

  return (
    <div className="login-background">
      <Container  style={{margin:"10px", paddingTop: '10px' }}>
        <Row className="justify-content-center align-items-center " style={{ minHeight: '100vh'  }}>
          <Col xs={12} md={6} lg={4}>
            <Card style={{ background: 'rgba(0,0,0,0.6)', color: 'white', borderRadius: '12px', padding: '2rem' }}>
              <h2 className="text-center mb-4 fw-bold"> Login</h2>

              {errorMsg && (
                <Alert variant="danger" dismissible onClose={() => setErrorMsg('')}>
                  {errorMsg}
                </Alert>
              )}

              <Form onSubmit={handleSubmit}>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label>Email Address</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Enter your email address"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>

                <Form.Group className="mb-3" controlId="formBasicPassword">
                  <Form.Label>Password</Form.Label>
                  <Form.Control
                    type="password"
                    placeholder="Enter your password"
                    name="password"
                    value={formData.password}
                    onChange={handleChange}
                    required
                  />
                </Form.Group>

                <div className="d-flex justify-content-center gap-3 mt-4">
                  <Button variant="primary" type="submit">
                    Login
                  </Button>
                  <Button variant="secondary" style={{ background: 'red' }} type="button" onClick={() => setFormData({ email: '', password: '' })}>
                    Cancel
                  </Button>
                </div>
              </Form>

              <p className="mt-3 text-center">
                <span style={{ color: 'white' }}>Dont have an account? </span>
                <Link to="/register" style={{ color: 'yellow', textDecoration: 'none', fontWeight: 'bold' }}>
                  Register here
                </Link>
              </p>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Login;
