import React, { useState } from "react";
import { Form, Button, Container, Row, Col, Card, Alert } from "react-bootstrap";
import { useNavigate, Link } from "react-router-dom";

function Register() {
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    confirmPassword: "",
  });
  const [showSuccess, setShowSuccess] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const navigate = useNavigate();

  const handleChange = (e) => {
    setFormData((prev) => ({ ...prev, [e.target.name]: e.target.value }));
    setErrorMsg("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (formData.password !== formData.confirmPassword) {
      setErrorMsg("Passwords do not match.");
      return;
    }
    const users = JSON.parse(localStorage.getItem("users") || "[]");
    if (users.find((u) => u.email === formData.email)) {
      setErrorMsg("Email already registered. Please login instead.");
      return;
    }
    const newUser = {
      username: formData.username,
      email: formData.email,
      password: formData.password,
    };
    users.push(newUser);
    localStorage.setItem("users", JSON.stringify(users));

    setShowSuccess(true);

    setFormData({ username: "", email: "", password: "", confirmPassword: "" });
    setTimeout(() => {
      navigate("/login");
    }, 1000);
  };

  return (
    <div className="register-background">
    
      <Container>
        <Row className="justify-content-center">
          <h6 style={{color:"white",textAlign:"left",fontSize:"30px",fontWeight:"bold"}}>Register here to view dashboard</h6>
          <Col xs={12} md={6}>
            <Card
              style={{ background: "rgba(0,0,0,0.6)", color: "white", borderRadius: "8px" }}
            >
              <Card.Body>
                <h3 className="text-center mt- pt-3 fw-bold">Register</h3>
                 {errorMsg && (
                  <Alert variant="danger" onClose={() => setErrorMsg("")} dismissible>
                    {errorMsg}
                  </Alert>
                )}

                {showSuccess && (
                  <Alert variant="success" dismissible>
                    Registered successfully! Redirecting to login...
                  </Alert>
                )}

                <Form onSubmit={handleSubmit} className="p-4">
                  <Form.Group className="mb-3" controlId="formBasicUserName">
                    <Form.Label>Username</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter username"
                      name="username"
                      value={formData.username}
                      onChange={handleChange}
                      required
                    />
                  </Form.Group>

                  <Form.Group className="mb-3" controlId="formBasicEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="email"
                      placeholder="Enter your email address"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      required
                    />
                  </Form.Group>

                  <Form.Group className="mb-3" controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Enter password"
                      name="password"
                      value={formData.password}
                      onChange={handleChange}
                      required
                    />
                  </Form.Group>

                  <Form.Group className="mb-3" controlId="formBasicConfirmPassword">
                    <Form.Label>Confirm Password</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Confirm password"
                      name="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      required
                    />
                  </Form.Group>

                  <div className="d-flex gap-3 mt-3 justify-content-center">
                    <Button variant="primary" type="submit">
                      Register
                    </Button>
                    <Button
                      variant="secondary"
                      style={{ background: "red" }}
                      type="button"
                      onClick={() =>
                        setFormData({ username: "", email: "", password: "", confirmPassword: "" })
                      }
                    >
                      Reset
                    </Button>
                  </div>
                </Form>

                <p className="mt-3 text-center" style={{ fontWeight: "bold" }}>
                  Already have an account? <Link to="/login" style={{ color: "#0d6efd" }}>Login here</Link>
                </p>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Register;
